import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import * as OAuth from '@zalando/oauth2-client-js';

import { Nav, Navbar, Container } from 'react-bootstrap';
import ImgurAlbums from './ImgurAlbums';
import Callback from './Callback';
import ImgurAlbumView from './ImgurAlbumView';

class App extends React.Component {

  imgur = new OAuth.Provider({
    id: 'imgur',   // required
    authorization_url: 'https://api.imgur.com/oauth2/authorize' // required
  });

  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false
    }
  }
  fetchMyAccount = () => {
    var accessToken = this.imgur.getAccessToken();
    var headers = {
      'Authorization': `Bearer ${accessToken}`
    };
    var url = `https://api.imgur.com/3/account/me`;
    return fetch(url, { headers: headers })
  }

  componentDidMount() {
    if (this.imgur.hasAccessToken()) {
      this.fetchMyAccount().then(
        res => res.json()
      ).then(
        (result) => {
          if (result.data.error) {
            this.handleLogin();
          } else {
            this.setState({ loggedIn: true })
          }
        },
        (error) => { this.handleLogin(); }
      )
    } else {
      this.handleLogin();
    }
  }

  handleLogin = () => {
    var request = new OAuth.Request({
      client_id: 'b69efa858e8dc56',  // required
      redirect_uri: 'https://rogershen.github.io/imgur-album-view/callback'
    });

    var uri = this.imgur.requestToken(request);

    this.imgur.remember(request);

    window.location.href = uri;
  }

  render() {
    var loggedIn = this.state.loggedIn;
    return (
      <Router>
        <div className="App">
          <Navbar bg="dark" variant="dark">
            <Navbar.Brand>Imgur Album View</Navbar.Brand>
            <Nav>
              <Nav.Link href="/imgur-album-view/albums">My Albums</Nav.Link>
            </Nav>
          </Navbar>
          <br />
          <Container>
            {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
            <Switch>
              <Route path="/imgur-album-view/albums/:id" component={ImgurAlbumView} />
              <Route path="/imgur-album-view/albums">
                {loggedIn ? <ImgurAlbums /> : "Logging in..."}
              </Route>
              <Route path="/callback">
                {loggedIn ? <Redirect to="/imgur-album-view/albums" /> :
                  <Callback handleLogin={() => this.setState({ loggedIn: true })} />}
              </Route>
            </Switch>
          </Container>
        </div>
      </Router>
    );
  }
}

export default App;
