import React from 'react';
import * as OAuth from '@zalando/oauth2-client-js';
import { ButtonToolbar, ToggleButton, ButtonGroup, ToggleButtonGroup, FormControl } from 'react-bootstrap';
import { Container, Row, Col } from 'react-bootstrap';
import ReactDropdown from './ReactDropdown';
import ImgurAccordion from './ImgurAccordion';
import * as qs from 'query-string';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Utils from './Utils.js';

class ImgurAlbums extends React.Component {
  imgur = new OAuth.Provider({
    id: 'imgur',   // required
    authorization_url: 'https://api.imgur.com/oauth2/authorize' // required
  });

  static propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired
  }

  constructor(props) {
    super(props);
    this.state = {
      isFetchingData: false,
      albumsFetched: 0,
      albums: [],
      viewBy: "1",
      sortBy: "1",
      searchBy: null
    }
  }

  getAccessHeaders = () => {
    var accessToken = this.imgur.getAccessToken();
    return {
      'Authorization': `Bearer ${accessToken}`
    };
  }

  fetchAlbumCount = () => {
    var url = `https://api.imgur.com/3/account/me/albums/count`;
    return fetch(url, { headers: this.getAccessHeaders() })
  }

  fetchAlbums = (page) => {
    var url = `https://api.imgur.com/3/account/me/albums/${page}`;
    return fetch(url, { headers: this.getAccessHeaders() })
  }

  componentDidMount() {
    var params = qs.parse(this.props.location.search);
    if (params.viewBy) {
      this.setState({ viewBy: params.viewBy });
    }
    if (params.sortBy) {
      this.setState({ sortBy: params.sortBy });
    }
    this.setState({ albums: [], albumsFetched: 0, isFetchingData: true });

    this.fetchAlbumCount()
      .then(
        res => res.json()
      )
      .then(
        (result) => {
          var numAlbums = result.data;
          var numPages = Math.ceil(numAlbums / 50);
          for (var page = 0; page < numPages; page++) {
            this.fetchAlbums(page)
              .then(res => res.json())
              .then(
                (result) => {
                  this.setState(
                    function (state, props) {
                      if (state.albumsFetched + 1 === numPages) {
                        return {
                          albums: state.albums.concat(result.data),
                          albumsFetched: state.albumsFetched + 1,
                          isFetchingData: false
                        }
                      } else {
                        return {
                          albums: state.albums.concat(result.data),
                          albumsFetched: state.albumsFetched + 1
                        }
                      }
                    });
                }, (error) => {
                  console.log(error);
                }
              );
          }
        }, (error) => {
          console.log(error);
        }
      )
  }

  handleViewBy = (e) => {
    this.props.history.push({
      search: `?viewBy=${e.target.value}&sortBy=${this.state.sortBy}`
    });
    this.setState({ viewBy: e.target.value });
  }

  handleSortBy = (eventKey) => {
    this.props.history.push({
      search: `?viewBy=${this.state.viewBy}&sortBy=${eventKey}`
    });
    this.setState({ sortBy: eventKey });
  }

  handleSearchBy = Utils.debounce((searchBy) => {
    this.setState({ searchBy: searchBy })
  }, 250)

  render() {
    var alphabeticalComparator = (a, b) => {
      return a.title.toLowerCase() < b.title.toLowerCase() ? -1 : 1;
    };
    var timeComparator = (a, b) => {
      return a.datetime < b.datetime ? -1 : 1;
    };
    var albums = this.state.albums;
    var albumView = <React.Fragment />;
    if (!this.state.isFetchingData) {
      if (this.state.searchBy) {
        albums = albums.filter(album => album.title.toLowerCase().includes(this.state.searchBy.toLowerCase()));
      }
      switch (this.state.sortBy) {
        case "1":
          albums = albums.sort(alphabeticalComparator);
          break;
        case "2":
          albums = albums.sort(alphabeticalComparator).reverse();
          break;
        case "3":
          albums = albums.sort(timeComparator).reverse();
          break;
        case "4":
          albums = albums.sort(timeComparator);
          break;
        default:
          albumView = <React.Fragment />;
      }
      switch (this.state.viewBy) {
        case "1":
          albumView = <ImgurAccordion albums={albums} />;
          break;
        default:
          albumView = <React.Fragment />;
      }
    }
    return (
      <React.Fragment>
        <Container>
          <Row>
            <Col xs={6}>
              <Row>
                <ButtonGroup>
                  <FormControl placeholder="Search" onChange={(e) => this.handleSearchBy(e.target.value)} />
                  <ReactDropdown title="Sort By" variant="secondary"
                    valueSetter={(eventKey, event) => { this.handleSortBy(eventKey) }}
                    currentEventKey={this.state.sortBy}
                    items={["A-Z", "Z-A", "Newest", "Oldest"]} />
                </ButtonGroup>
              </Row>
            </Col>
            <Col className="unpadded-col" xs={6}>
              <span className="float-right">
                <ButtonToolbar onChange={this.handleViewBy}>
                  <ToggleButtonGroup type="radio" name="options" defaultValue={"1"} >
                    <ToggleButton variant="secondary" value={"1"}><i className="material-icons">view_day</i></ToggleButton>
                    <ToggleButton variant="secondary" value={"2"}><i className="material-icons">view_module</i></ToggleButton>
                    <ToggleButton variant="secondary" value={"3"}><i className="material-icons">dashboard</i></ToggleButton>
                  </ToggleButtonGroup>
                </ButtonToolbar>
              </span>
            </Col>
          </Row>
        </Container>

        <br />
        {albumView}
      </React.Fragment>

    );
  }
}

export default withRouter(ImgurAlbums);
