import React from 'react';
import { Accordion, Card, Row, Col, Container, Modal, Button, Image, Figure } from 'react-bootstrap';
import * as OAuth from '@zalando/oauth2-client-js';
import scrollIntoView from 'scroll-into-view-if-needed';

class ImgurAccordion extends React.Component {

  imgur = new OAuth.Provider({
    id: 'imgur',   // required
    authorization_url: 'https://api.imgur.com/oauth2/authorize' // required
  });

  constructor(props) {
    super(props);
    this.state = {
      screenWidth: window.innerWidth,
      currAlbum: null
    }
  }

  updateWindowDimensions = () => {
    this.setState({ screenWidth: window.innerWidth });
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);
  }

  getAccessHeaders = () => {
    var accessToken = this.imgur.getAccessToken();
    return {
      'Authorization': `Bearer ${accessToken}`
    };
  }

  fetchAlbum = (albumHash) => {
    var url = `https://api.imgur.com/3/album/${albumHash}`;
    return fetch(url, { headers: this.getAccessHeaders() });
  }

  handleAlbumClick = (id, e) => {
    e.preventDefault();
    this.fetchAlbum(id)
      .then(
        res => res.json()
      )
      .then(
        (result) => {
          this.setState({ currAlbum: result.data });
          scrollIntoView(document.getElementById(id));
        }, (error) => {
          console.log(error);
        }
      )
  }

  generateImageBlocks = (images) => {
    function gen4() {
      return Math.random().toString(16).slice(-4)
    }

    var screenWidth = this.state.screenWidth;
    var cols = screenWidth > 992 ? 3 : 2;
    var colIter = 0;
    var imageCols = [];
    for (let iter = 0; iter < images.length; iter++) {
      let image = images[iter];
      if (imageCols[colIter]) {
        imageCols[colIter].push(image);
      } else {
        imageCols[colIter] = [image];
      }
      colIter = colIter + 1 === cols ? 0 : colIter + 1;
    }

    return imageCols.map((imageCol) => {
      return (
        <Col xs={12} md={6} lg={4} className="unpadded-col" key={gen4()}>
          {imageCol.map(
            (image) => {
              return <React.Fragment key={image.id}>
                <Card.Img className="mb-3" src={image.link} onClick={() => this.setState({ [image.id]: true })} />
                <div><ImgurVerticallyCenteredModal image={image}
                  show={this.state[image.id]} onHide={() => this.setState({ [image.id]: false })} />
                </div>
              </React.Fragment>
            }
          )}
        </Col>
      );
    })
  }

  componentDidUpdate() {

  }

  render() {
    var albums = this.props.albums;
    var cards = [];
    var currAlbum = this.state.currAlbum;
    if (currAlbum && albums && !albums.map(album => album.id).includes(currAlbum.id)) {
      // Collapse all panels of accordion
      let accordion = document.getElementsByClassName("accordion")[0];
      for (let iter = 0; iter < accordion.children.length; iter++) {
        let card = accordion.children[iter];
        card.getElementsByClassName("collapse")[0].classList.remove("show");
      }
    }
    for (let iter = 0; iter < albums.length; iter++) {
      let album = albums[iter];
      let images = null;
      if (currAlbum && album.id === currAlbum.id) {
        images = currAlbum.images;
      }
      cards.push(
        <Card key={album.id}>
          <Accordion.Toggle id={album.id} onClick={(e) => this.handleAlbumClick(album.id, e)} as={Card.Header} eventKey={album.id}>
            <Container>
              <Row>
                <Col xs={8}>
                  <Row>
                    <Figure.Image
                      className="cover-icon"
                      width={32}
                      height={32}
                      src={`https://imgur.com/${album.cover}.jpg`}
                      fluid={false}
                    />
                    {album.title}
                  </Row>
                </Col>
                <Col xs={4}><span className="float-right">+</span></Col>
              </Row>
            </Container>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey={album.id}>
            <Card.Body>
              <Container>
                <Row>
                  {images ?
                    this.generateImageBlocks(images, album.id)
                    : "Loading..."}
                </Row>
              </Container>
              <Card.Text>
                <a target="_blank" rel="noopener noreferrer"
                  href={`https://imgur.com/a/${album.id}`}>Click to see this album on Imgur</a>
              </Card.Text>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      );
    }
    return (
      <Accordion>
        {cards}
      </Accordion >
    );
  }
}

function ImgurVerticallyCenteredModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      animation={false}
    >
      <Modal.Body>
        <Image src={props.image.link} className="img-responsive-ht" />
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ImgurAccordion;
