import React from 'react';
import { Dropdown, DropdownButton } from 'react-bootstrap';

class ReactDropdown extends React.Component {

    render() {
        var title = this.props.title;
        var variant = this.props.variant ? this.props.variant : "secondary";
        var currentEventKey = this.props.currentEventKey ? this.props.currentEventKey : "1";
        var valueSetter = this.props.valueSetter;
        var items = this.props.items;
        var i = 1;
        var dropdownItems = [];
        for (let iter = 0; iter < items.length; iter++) {
            let item = items[iter];
            dropdownItems.push(
                <Dropdown.Item onSelect={valueSetter}
                    eventKey={String(i)}
                    key={String(i)}
                    active={currentEventKey === String(i)}>
                    {item}
                </Dropdown.Item>
            )
            i++;
        }
        return (
            <DropdownButton
                title={title}
                variant={variant}
                id={`${title}-dropdown`}
            >
                {dropdownItems}
            </DropdownButton >
        );
    }
}

export default ReactDropdown;
