import React from 'react';
import * as OAuth from '@zalando/oauth2-client-js';

class Callback extends React.Component {

  imgur = new OAuth.Provider({
    id: 'imgur',   // required
    authorization_url: 'https://api.imgur.com/oauth2/authorize' // required
  });

  constructor(props) {
    super(props);
    this.props.handleLogin();
  }

  componentDidMount() {
    var search = window.location.search;
    if (window.location.hash) {
      var combinedHash = window.location.hash + '&' + search.substring(1, search.length);
      this.imgur.parse(combinedHash);
    }
  }

  render() {
    return (
      <React.Fragment />
    );
  }
}

export default Callback;
