import React from 'react';
import * as OAuth from '@zalando/oauth2-client-js';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

class ImgurAlbumView extends React.Component {
    static propTypes = {
        match: PropTypes.object.isRequired
    }
    imgur = new OAuth.Provider({
        id: 'imgur',   // required
        authorization_url: 'https://api.imgur.com/oauth2/authorize' // required
    });

    render() {
        return <div>{this.props.match.params.id}</div>
    }
}

export default withRouter(ImgurAlbumView);